header {
    background: #f1ebcb;
    text-align: left;
    width: 100%;
    padding: 30px 0;
    margin-bottom: 120px;
}

.header-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 80%;
    margin: 0 auto;
}

h1.name {
    font-size: 40px;
    color: #333;
    margin: 0;
}
h1.name:hover {
    color: #3a1b3d;
}



nav a {
    font-size: 28px;
    padding: 10px 15px;
    color: #333;
    margin: 0 8px;
    text-decoration: none;
    transition: background-color 0.3s ease, color 0.3s ease;
}

nav a:hover {
    background-color: #ddd;
    color: #000;
}






.logos {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-top: 10px;
}
.logo {
    height: 70px;
    width: auto;
    transition: transform 0.3s ease;
}
.logo:hover {
    transform: scale(1.1);
}



@media screen and (max-width: 768px) {
    header {
        padding: 20px 0;
    }

    .header-container {
        width: 90%;
    }

    h1.name {
        font-size: 30px;
    }

    nav a {
        font-size: 20px;
        padding: 8px 12px;
    }

    .logos {
        margin-top: 5px;
    }

    .logo {
        height: 50px;
    }
    
}