footer {
    background: #f1ebcb;
    text-align: center;
    padding: 20px;
    margin-top: 80px;
    left: 0;
    bottom: 0;
}

.social-logo {
    transition: transform 0.5s ease;
    width: 50px;
    margin: 0 10px;
}

.social-logo:hover {
    transform: scale(1.1);
}

/* Ensure links do not have underlines */
.footer-container a {
    text-decoration: none;
}




@media screen and (max-width: 768px){
    footer {
        padding: 10px;
    }

    .social-logo {
        width: 40px;
    }
}